import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";
import Header from "Components/Header";
import Home from "Routes/Home";
import TV from "Routes/TV";
import Search from "Routes/Search";
import Detail from "Routes/Detail";

export default () => (
  <Router>
    <>
      <Header />
      <Switch>
        {" "}
        {/* 두개의 컴포넌트가 동시에 rendering 되는것을 방지해주는 element */}
        <Route path="https://yuiop8747.github.io" exact={true} component={Home}></Route> {/* router는 route에게 기본적으로 props를 전달해준다. */}
        <Route path="https://yuiop8747.github.io/tv" component={TV}></Route>
        <Route path="https://yuiop8747.github.io/search" component={Search}></Route>
        <Route path="https://yuiop8747.github.io/movie/:id" component={Detail}/>
        <Route path="https://yuiop8747.github.io/show/:id" component={Detail}/>
        <Redirect from="*" to="https://yuiop8747.github.io" />
        {/* 일치하는 url이 하나도 없다면, to로 보낸다. */}
      </Switch>
    </>
  </Router>
);
